import React, { ChangeEvent, PureComponent, SyntheticEvent } from 'react';
import { LegacyForms } from '@grafana/ui';
import { DataSourcePluginOptionsEditorProps, SelectableValue } from '@grafana/data';
import { defaultValues, MyDataSourceOptions } from './types';

const { Switch, Select, Input } = LegacyForms;

interface Props extends DataSourcePluginOptionsEditorProps<MyDataSourceOptions> {}

interface State {}

const protocolOptions: Array<SelectableValue<string>> = [
  { label: 'ws', value: 'ws' },
  { label: 'wss', value: 'wss' },
];

export class ConfigEditor extends PureComponent<Props, State> {
  onChangeProtocol = (item: SelectableValue<string>) => {
    this.onInputChange('protocol', item.value);
  };

  onHostChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('host', event.target.value);
  };

  onPortChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('port', Number(event.target.value));
  };

  onAuthenticationChange = (event: SyntheticEvent<HTMLInputElement>) => {
    const authentication = (event.target as HTMLInputElement).checked;
    this.onInputChange('authentication', authentication);
  };

  onUsernameChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('username', event.target.value);
  };

  onPasswordChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('password', event.target.value);
  };

  onInputChange(key: string, value: any) {
    const { onOptionsChange, options } = this.props;
    const jsonData = {
      ...options.jsonData,
      [key]: value,
    };
    onOptionsChange({ ...options, jsonData });
  }

  render() {
    const { options } = this.props;
    const { jsonData } = options;
    return (
      <div className="gf-form-group">
        <div className="gf-form-inline">
          <div className="gf-form width-10">
            <Select
              className={'width-10'}
              options={protocolOptions}
              onChange={this.onChangeProtocol}
              value={{
                value: jsonData.protocol || defaultValues.protocol,
                label: `Protocol (${jsonData.protocol || defaultValues.protocol})`,
              }}
            />
          </div>
          <div className="gf-form width-16">
            <Input placeholder="Host" onChange={this.onHostChange} value={jsonData.host || defaultValues.host} />
          </div>
          <div className="gf-form width-4">
            <Input
              placeholder="Port"
              type="number"
              onChange={this.onPortChange}
              value={jsonData.port || defaultValues.port}
            />
          </div>
        </div>
        <div className="gf-form-inline">
          <div className="gf-form">
            <Switch
              label="Authentication"
              checked={jsonData.authentication || defaultValues.authentication}
              onChange={this.onAuthenticationChange}
            />
          </div>
        </div>
        {jsonData.authentication && (
          <div className="gf-form-inline">
            <div className="gf-form width-15">
              <Input
                placeholder="Username"
                onChange={this.onUsernameChange}
                value={jsonData.username || defaultValues.username}
              />
            </div>
            <div className="gf-form width-15">
              <Input
                placeholder="Password"
                type="password"
                onChange={this.onPasswordChange}
                value={jsonData.password || defaultValues.password}
              />
            </div>
          </div>
        )}
      </div>
    );
  }
}
