import { DataQuery, DataSourceJsonData } from '@grafana/data';

export interface MyQuery extends DataQuery {
  hasManualTopic: boolean;
  topic: string;
  manualTopic?: string;
  topicSelector?: string;
  topicSelected?: string;
  member: string;
  legend: string;
  messaging: Messaging;
  hasResponseSelector: boolean;
}

export enum Messaging {
  DEFAULT = 'DEFAULT',
  REQ_RES = 'REQ_RES',
}

export const defaultQuery: Partial<MyQuery> = {
  hasManualTopic: false,
  topic: '',
  manualTopic: '',
  topicSelector: '',
  topicSelected: '',
  member: 'value',
  legend: 'value',
  messaging: Messaging.DEFAULT,
  hasResponseSelector: true,
};

/**
 * These are options configured for each DataSource instance
 */
export interface MyDataSourceOptions extends DataSourceJsonData {
  protocol: string;
  host: string;
  port: number;
  authentication: boolean;
  username: string;
  password: string;
}

export const defaultValues: MyDataSourceOptions = {
  protocol: 'ws',
  host: '',
  port: 9001,
  authentication: false,
  username: '',
  password: '',
};
