import defaults from 'lodash/defaults';

import React, { ChangeEvent, PureComponent, SyntheticEvent } from 'react';
import { InlineFormLabel, LegacyForms, Select, Spinner, Switch } from '@grafana/ui';
import { QueryEditorProps, SelectableValue } from '@grafana/data';
import { DataSource } from './DataSource';
import { defaultQuery, MyDataSourceOptions, MyQuery, Messaging } from './types';
import { Debounce } from 'lodash-decorators';
import { default as mqtt, MqttClient } from 'mqtt';

const { FormField } = LegacyForms;
const messagingOptions: Array<SelectableValue<string>> = [
  { label: Messaging.DEFAULT, value: Messaging.DEFAULT },
  { label: Messaging.REQ_RES, value: Messaging.REQ_RES },
];

type Props = QueryEditorProps<DataSource, MyQuery, MyDataSourceOptions>;
type States = { topicSelector: string; mqttSelectorOptions: Array<SelectableValue<string>>; loading: boolean };

export class QueryEditor extends PureComponent<Props, States> {
  client: MqttClient;

  constructor(props: Props) {
    super(props);
    const {
      datasource: { mqttOptions },
      query: { topicSelector },
    } = this.props;

    this.state = {
      topicSelector: topicSelector || '',
      mqttSelectorOptions: [],
      loading: true,
    };

    this.client = mqtt.connect({ ...mqttOptions });
    if (topicSelector) {
      this.client.subscribe(topicSelector);
    }
    this.subscribeMessage();
  }

  componentDidMount(): void {
    // Execute query when we open editor, so it will query data for that editor and editor gets value
    this.handleMqttRunQuery();
  }

  componentWillUnmount(): void {
    if (this.client) {
      this.client.end();
    }
  }

  private subscribeMessage() {
    const that = this;
    this.client.on('message', function(replyTopic: string, message: any) {
      try {
        const parsedMessage = JSON.parse(message.toString());
        const mqttSelectorOptions: Array<SelectableValue<string>> = [];
        if (Array.isArray(parsedMessage)) {
          parsedMessage.forEach(item => {
            mqttSelectorOptions.push({ label: item, value: item });
          });
        }
        that.setState({ mqttSelectorOptions, loading: false });
      } catch (e) {}
    });
  }

  @Debounce(1000)
  handleMqttRunQuery() {
    const { onRunQuery, onChange, query } = this.props;
    const { hasManualTopic, manualTopic, topicSelector, topicSelected } = query;
    let topic = '<<reserved>>'; // by default topic
    if (!hasManualTopic && topicSelector) {
      topic = `${topicSelector}/${topicSelected}`;
    } else if (hasManualTopic && manualTopic) {
      topic = manualTopic;
    }
    onChange({ ...query, topic });
    onRunQuery();
  }

  onChangeManualTopic = (event: SyntheticEvent<HTMLInputElement>) => {
    const { onChange, query, onRunQuery } = this.props;
    const hasManualTopic = (event.target as HTMLInputElement).checked;
    onChange({ ...query, hasManualTopic });
    onRunQuery();
  };

  onManualTopicChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { onChange, query } = this.props;
    onChange({ ...query, manualTopic: event.target.value });
    this.handleMqttRunQuery();
  };

  onTopicSelectorChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { onChange, query } = this.props;
    const { topicSelector } = this.state;
    const topicSelectorValue = event.target.value;
    this.setState({ mqttSelectorOptions: [] });
    if (topicSelectorValue && topicSelector !== topicSelectorValue && this.client) {
      this.setState({ loading: true });
      this.client.unsubscribe(topicSelector);
      this.client.subscribe(topicSelectorValue);
      this.setState({ topicSelector: topicSelectorValue });
    }
    onChange({ ...query, topicSelector: topicSelectorValue });
    this.handleMqttRunQuery();
  };

  onTopicSelectedChange = (event: SelectableValue<string>) => {
    const { onChange, query } = this.props;
    onChange({ ...query, topicSelected: event.value });
    this.handleMqttRunQuery();
  };

  onChangeMessaging = (event: SelectableValue<string>) => {
    const { onChange, query, onRunQuery } = this.props;
    onChange({ ...query, messaging: event.value as Messaging });
    onRunQuery();
  };

  onChangeHasResponseSelector = (event: SyntheticEvent<HTMLInputElement>) => {
    const { onChange, query, onRunQuery } = this.props;
    onChange({ ...query, hasResponseSelector: (event.target as HTMLInputElement).checked });
    onRunQuery();
  };

  onMemberChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { onChange, query, onRunQuery } = this.props;
    onChange({ ...query, member: event.target.value });
    onRunQuery();
  };

  onLegendChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { onChange, query, onRunQuery } = this.props;
    onChange({ ...query, legend: event.target.value });
    onRunQuery();
  };

  render() {
    const query = defaults(this.props.query, defaultQuery);
    const { mqttSelectorOptions, loading } = this.state;
    const {
      hasManualTopic,
      manualTopic,
      topicSelector,
      topicSelected,
      member,
      legend,
      messaging,
      hasResponseSelector,
    } = query;
    return (
      <div className="gf-form-group">
        <div className="gf-form">
          <InlineFormLabel width={12}>Manual Topic</InlineFormLabel>
          <div style={{ margin: 'auto 0' }}>
            <Switch width={12} onChange={this.onChangeManualTopic} value={hasManualTopic} />
          </div>
        </div>
        <div className="gf-form">
          {hasManualTopic ? (
            <FormField
              inputWidth={24}
              labelWidth={12}
              value={manualTopic}
              onChange={this.onManualTopicChange}
              label="MQTT Topic"
              type="string"
              placeholder="some/mqtt/topic"
            />
          ) : (
            <FormField
              inputWidth={24}
              labelWidth={12}
              value={topicSelector}
              onChange={this.onTopicSelectorChange}
              label="Topic selector"
              type="string"
              placeholder="some/mqtt/topic-selector"
            />
          )}
        </div>
        {!hasManualTopic && (
          <div className="gf-form">
            <InlineFormLabel width={12}>Select Topic</InlineFormLabel>
            <Select
              width={24}
              options={mqttSelectorOptions}
              onChange={this.onTopicSelectedChange}
              value={topicSelected}
              disabled={loading}
            />
            {loading && (
              <div style={{ height: '32px', marginLeft: '5px', display: 'flex', alignItems: 'center' }}>
                <Spinner size={18} />
              </div>
            )}
          </div>
        )}
        <div className="gf-form">
          <InlineFormLabel width={12}>Messaging</InlineFormLabel>
          <Select
            width={24}
            options={messagingOptions}
            onChange={this.onChangeMessaging}
            value={messagingOptions.find(v => v.value === (messaging || Messaging.DEFAULT))}
          />
        </div>
        <div className="gf-form">
          <InlineFormLabel width={12}>Response Selector</InlineFormLabel>
          <div style={{ margin: 'auto 0' }}>
            <Switch width={12} onChange={this.onChangeHasResponseSelector} value={hasResponseSelector} />
          </div>
        </div>
        {hasResponseSelector && (
          <div className="gf-form">
            <FormField
              inputWidth={24}
              labelWidth={12}
              value={member}
              onChange={this.onMemberChange}
              label="JSON member"
              type="string"
              placeholder="some.member"
            />
          </div>
        )}
        <div className="gf-form">
          <FormField
            inputWidth={24}
            labelWidth={12}
            value={legend}
            onChange={this.onLegendChange}
            label="Legend"
            type="string"
            placeholder="Legend"
          />
        </div>
      </div>
    );
  }
}
